body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navBarTitle {
    color: black;
    border-style: solid;
    font-family: 'Roboto', sans-serif;
    font-size: 40px;
    font-weight: 600;
    margin-top: 20px;
    margin-bottom: 20px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    letter-spacing: 1px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.navButtonBar {
    margin-left: auto;
}
.tabsSize {
    font-size: 100px;
}
.spacers {
    padding-right: 10px;
}
.customizeNavBar {
    min-height: 0px;
    display: flex;
    flex-direction: row;
    padding-left: 24px;
    padding-right: 24px;
    background: #F0FFFF;
    border-bottom-style: solid;
    border-color: black;
    align-items: center;
}
.navBarButtonFormat {
  color:black;
  font-size: 30px;
  font-weight: 500;
  font-family: 'Roboto', sans-serif;
  margin-left: 10px;
  margin-top: 12px;
}
.navBarHHLogo {
  width: 64px;
  height: 64px;
}

@media screen and (min-width: 426px) and (max-width: 1024px) {
  .navBarTitle {
    text-align: center;
    margin-left: 60px;
    margin-right: 60px;
    margin-bottom: 10px;
    font-size: 30px;
    align-self: center;
  }
  .navButtonBar {
      margin-left: 20px;
      margin-right: 20px;
      padding-top: 0px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      text-align: center;
  }
  .customizeNavBar {
      min-height: 0px;
      display: flex;
      flex-direction: column;
      padding-left: 0px;
      padding-right: 0px;
  }
}

@media screen and (max-width: 425px) {
  .navBarTitle {
    text-align: center;
    margin-left: 30px;
    margin-right: 30px;
    margin-bottom: 10px;
    font-size: 30px;
    align-self: center;
  }
  .navButtonBar {
    margin-left: 0px;
    padding-top: 0px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
  }
  .customizeNavBar {
    min-height: 0px;
    display: flex;
    flex-direction: column;
    padding-left: 0px;
    padding-right: 0px;
  }
}
.contactFont {
  font-family: 'Roboto', sans-serif;
  text-align: center;
  vertical-align: middle;
}

.contactTitle {
  margin-bottom: 40px;
  margin-top: 40px;
  font-size: 40px;
}

.contactPictureandTextBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.contactPictureFormat {
  margin-right: 15px;
}

.contactTextFormat {
  margin-left: 15px;
  font-size: 30px;
}
.homepageContainer {
  display: flex;
  flex-direction: row;
  margin: 20px;
}
.homepageHHPic {
  margin: 20px;
}
.homepageIntroTitle {
  margin: 20px;
}

@media screen and (min-width: 426px) and (max-width: 1024px) {
  .homepageContainer {
    flex-direction: column;
  }
  .homepageHHPic {
    align-self: center;
  }
  .homepageIntroTitle {
    
  } 
  .homepageTextBody {
    margin-top: 20px;
  }
}

@media screen and (max-width: 425px) {
  .homepageContainer {
    flex-direction: column;
  }
  .homepageHHPic {
    align-self: center;
    margin: 0px;
  }
  .homepageIntroTitle {
    text-align: center;
    margin: 0px;
  } 
